.container {
  display: block;
  width: 100%;
  padding: 0 20px;
  margin-right: auto;
  margin-left: auto;

  @media screen and (max-width: $bp-mobile-max) {
    max-width: $bp-mobile;
  }

  @media screen and (min-width: $bp-tablet) {
    width: $bp-tablet;
  }

  @media screen and (min-width: $bp-desktop) {
    width: $bp-desktop;
  }
}

body {
  display: flex;
  flex-direction: column;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: normal;
  color: $primary-color;
  min-height: 100vh;
}

main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.section {
  flex-grow: 1;
}
